.card {
  background-color: hsl(185deg, 41%, 84%);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.card-body {
  min-width: 50rem;
  min-height: 30vh;
  background-color: hsl(189deg, 41%, 97%);
  padding: 3rem;
  gap: 3rem;
  border-radius: 2rem;
  display: flex;
}
@media only screen and (max-width: 31.25em) {
  .card-body {
    min-width: 20rem;
    display: grid;
    justify-items: center;
  }
}
.card-body .bill-tip form {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  min-width: 20rem;
  max-width: 27rem;
}
.card-body .bill-tip form .label-input {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.card-body .bill-tip form .label-input .message {
  display: none;
}
.card-body .bill-tip form .label-input .tips {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.card-body .bill-tip form .label-input .tips input[type=button] {
  width: 7rem;
  background-color: hsl(183deg, 100%, 15%);
  color: hsl(185deg, 41%, 84%);
  padding: 0.5rem 0.5rem;
  text-align: center;
  box-sizing: border-box;
  border-radius: 3px;
  border: none;
  font-weight: bold;
}
.card-body .bill-tip form .label-input .tips input[type=button]:focus {
  outline: none;
  border: 2px solid hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}
.card-body .bill-tip form .label-input .tips input[type=button]:hover, .card-body .bill-tip form .label-input .tips input[type=button]:focus {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
  border: none;
}
.card-body .bill-tip form .label-input .tips .tip {
  width: 7rem;
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
  padding: 0.5rem 0.5rem;
  text-align: end;
  box-sizing: border-box;
  border-radius: 3px;
  border: none;
  font-weight: bold;
}
.card-body .bill-tip form .label-input .tips .tip:focus {
  outline: none;
  border: 2px solid hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}
.card-body .bill-tip form .label-input .tips .tip::placeholder {
  text-align: center;
}
.card-body .bill-tip form .label-input .tips .input-message {
  border: 2px solid red;
}
.card-body .bill-tip form .label-input .tips .input-message:focus {
  border: 2px solid red;
}
.card-body .bill-tip form .label-input .input {
  width: 100%;
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
  padding: 0.5rem 0.8rem;
  text-align: end;
  box-sizing: border-box;
  border-radius: 3px;
  border: none;
  font-weight: bold;
}
.card-body .bill-tip form .label-input .input:focus {
  outline: none;
  border: 2px solid hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}
.card-body .bill-tip form .label-input .input::placeholder {
  position: absolute;
  left: auto;
  right: 0.8rem;
}
.card-body .bill-tip form .label-input .input-message {
  border: 2px solid red;
}
.card-body .bill-tip form .label-input .input-message:focus {
  border: 2px solid red;
}
.card-body .bill-tip form .label-input #bill {
  background-image: url(../images/icon-dollar.svg);
  background-repeat: no-repeat;
  background-position: 1rem 0.8rem;
  background-size: 0.8rem;
}
.card-body .bill-tip form .label-input #people {
  background-image: url(../images/icon-person.svg);
  background-repeat: no-repeat;
  background-position: 1rem 0.8rem;
  background-size: 0.8rem;
}
.card-body .total-amount {
  width: 27rem;
  min-width: 20rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  border-radius: 10px;
  background-color: hsl(183deg, 100%, 15%);
  color: hsl(189deg, 41%, 97%);
}
.card-body .total-amount .calculated {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.card-body .total-amount .calculated .side-text p {
  color: hsl(184deg, 14%, 56%);
}
.card-body .total-amount .calculated .calculated-price {
  justify-self: end;
  font-size: 2.5rem;
  font-weight: bolder;
  color: hsl(172deg, 67%, 45%);
}
.card-body .total-amount .reset {
  position: relative;
  top: 5rem;
  background-color: hsl(186deg, 14%, 43%);
  color: hsl(183deg, 100%, 15%);
  font-size: 1.5rem;
  font-weight: bold;
  border: none;
  padding: 0.5rem;
  border-radius: 3px;
}
.card-body .total-amount .reset:hover {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
}

p {
  font-size: 15px;
  font-weight: 400;
}

h1 {
  font-size: 20px;
  font-weight: 700;
  color: hsl(183deg, 100%, 15%);
  margin-bottom: 10px;
}

.label-input {
  font-size: 1.5rem;
  font-weight: bold;
}
.label-input p {
  font-size: 1rem;
  align-self: flex-end;
  color: red;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

html {
  font-size: 62.5%;
  color: hsl(183deg, 100%, 15%);
  font-family: "https://fonts.google.com/specimen/Overpass";
}

::selection {
  color: hsl(184deg, 14%, 56%);
}